<template>
  <v-container fluid>
    <v-card>
      <v-card-text class="d-flex">
        <h2 v-if="'hijriYear' in tender" class="mt-3">
          Eid-Ul-Adha {{ tender.hijriYear }}H
        </h2>
        <v-text-field
          label="Search"
          hide-details
          class="ml-3"
          v-model="searchKey"
        />
        <v-spacer></v-spacer>
        <v-btn @click="newData"> <v-icon icon="mdi-plus" />Add Expense</v-btn>
      </v-card-text>
    </v-card>

    <v-card class="mt-4">
      <template v-if="expenses.length == 0">
        <h4 class="ma-5 text-center">No Items Available</h4>
      </template>
      <v-list density="compact" v-else>
        <v-list-item
          v-for="item in filteredExpenses"
          :key="item.expenseId"
          :subtitle="item.particulars"
          density="compact"
        >
          <template v-slot:title>
            <div class="d-flex">
              {{ item.expenseHead.expenseHead }} - {{ item.description }}
              <v-spacer />
              <b class="text-red">{{ item.amount.toLocaleString() }}</b>
              <v-btn
                icon="mdi-pencil"
                variant="text"
                size="small"
                color="primary"
                class="ml-5"
                @click="editData(item)"
              />
              <v-btn
                icon="mdi-delete"
                variant="text"
                size="small"
                color="red"
                class="ml-5"
                @click="deleteData(item)"
              />
            </div>
          </template>
          <v-divider class="my-3" />
        </v-list-item>
      </v-list>
    </v-card>
    <v-card class="mt-4">
      <v-card-text>
        <h3>
          Total Expense: <span class="text-red">{{ totalExpense }}</span>
        </h3>
      </v-card-text>
    </v-card>
  </v-container>

  <simple-dialog
    ref="dlgExpense"
    title="New Expense"
    width="786"
    :closeOnSave="false"
    @ok="saveData"
  >
    <template v-slot:content>
      <v-form>
        <v-row>
          <v-col>
            <v-select
              label="Expense Head"
              v-model="expense.expenseHeadId"
              :items="expenseHeads"
              item-title="expenseHead"
              item-value="expenseHeadId"
            />
            <v-select
              label="Divisions"
              v-model="expense.description"
              :items="divisions"
            />
            <v-text-field label="Particulars" v-model="expense.particulars" />
            <v-text-field
              label="Amount"
              v-model="expense.amount"
              type="number"
            />
          </v-col>
        </v-row>
      </v-form>
    </template>
  </simple-dialog>
</template>

<script setup>
import { ref, reactive, onMounted, inject, watch } from "vue";
import {
  getActiveTender,
  getExpenseHeads,
  getExpensesByTenderId,
  newExpense,
  editExpense,
  deleteExpense,
} from "@/services/tenderService";
import { useRoute } from "vue-router";

const route = useRoute();

const snackbar = inject("snackBar");
const loader = inject("loader");

const dlgExpense = ref({});

const tender = ref({});
const expenseHeads = ref([]);
const expenses = ref([]);
const expense = reactive({
  expenseId: 0,
  tenderId: 0,
  expenseHeadId: null,
  description: "",
  particulars: "",
  amount: 0,
});

const divisions = [
  "Central",
  "Division 1",
  "Division 2",
  "Division 3",
  "Division 4",
  "Division 5",
  "Division 6",
  "Division 7",
  "Division 8",
  "Division 9",
  "Division 10",
  "Division 11",
  "Division 12",
  "Division 13",
  "Division 14",
  "Division 15",
  "Division 16",
  "Division 17",
  "Division 18",
];

const searchKey = ref([]);
const filteredExpenses = ref([]);
const totalExpense = ref(0);

async function loadExpenseHeads() {
  let result = await getExpenseHeads();
  expenseHeads.value = result;
}

async function loadTender() {
  let result = await getActiveTender();
  tender.value = result;
}

async function getData() {
  const tenderId = tender.value.tenderId;
  let result = await getExpensesByTenderId(tenderId);
  expenses.value = result;
  filteredExpenses.value = result;

  totalExpense.value = filteredExpenses.value.reduce((acc, item) => {
    return parseInt(acc) + parseInt(item.amount);
  });
}

async function saveData() {
  if (expense.expenseHeadId == null) {
    snackbar.error("Please select an expense head");
    return;
  }
  if (expense.particulars == "") {
    snackbar.error("Please enter particulars");
    return;
  }
  if (expense.amount == 0) {
    snackbar.error(
      `Please enter an amount(${expense.amount}) greater than zero`
    );
    return;
  }

  loader.show();

  var result = null;

  if (expense.expenseId == 0) {
    result = await newExpense(expense).finally(() => {
      loader.hide();
    });
  } else {
    result = await editExpense(expense.expenseId, expense).finally(() => {
      loader.hide();
    });
  }

  console.log(result);
  await getData();
  dlgExpense.value.hide();
}

async function deleteData(expense) {
  loader.show();

  var result = null;

  result = await deleteExpense(expense.expenseId).finally(() => {
    loader.hide();
  });

  console.log(result);
  await getData();
}

function newData() {
  expense.tenderId = tender.value.tenderId;
  expense.expenseHeadId = null;
  expense.particulars = "";
  expense.description = "";
  expense.amount = 0;

  dlgExpense.value.show();
}

function editData(exp) {
  expense.expenseId = exp.expenseId;
  expense.tenderId = tender.value.tenderId;
  expense.expenseHeadId = exp.expenseHeadId;
  expense.particulars = exp.particulars;
  expense.description = exp.description;
  expense.amount = exp.amount;

  dlgExpense.value.show();
}

function filterExpenses(searchKey) {
  searchKey = searchKey.toUpperCase();

  if (searchKey == "") {
    filterExpenses.value = expenses.value;
  } else {
    filteredExpenses.value = expenses.value.filter((exp) => {
      console.log(
        searchKey,
        exp.description,
        exp.description.includes(searchKey)
      );

      return (
        exp.description.toUpperCase().includes(searchKey) ||
        exp.particulars.toUpperCase().includes(searchKey) ||
        exp.expenseHead.expenseHead.toUpperCase().includes(searchKey)
      );
    });
  }

  totalExpense.value = filteredExpenses.value.reduce((acc, item) => {
    return parseInt(acc) + parseInt(item.amount);
  });
}

watch(searchKey, (newVal) => {
  filterExpenses(newVal);
});

console.log(expense);

onMounted(async () => {
  await loadTender();
  await loadExpenseHeads();
  await getData();

  route.meta.title = `Expenses (Eid-Ul-Adha ${tender.value.hijriYear})`;
});
</script>
