<template>
  <v-container fluid>
    <v-card>
      <v-card-text class="d-flex justify-end">
        <v-btn @click="newCustomer"
          ><v-icon icon="mdi-plus" />Add Customer</v-btn
        >
      </v-card-text>
    </v-card>
    <data-table class="mt-2" :tableData="tableData">
      <template v-slot:[`item.SNo`]="{ item }">
        {{ item.index + 1 }}
      </template>
      <template v-slot:[`item.Actions`]="{ item }">
        <v-btn size="small" @click="() => printForm(item.raw)" class="mr-2">
          <v-icon icon="mdi-printer" size="small" left></v-icon> Print Form
        </v-btn>
        <v-btn size="small" @click="() => editData(item.raw)">
          <v-icon icon="mdi-pencil" size="small" left></v-icon> Edit
        </v-btn>
      </template>
    </data-table>
  </v-container>

  <simple-dialog
    ref="dlgCustomer"
    title="New Customer"
    width="786"
    @ok="saveData"
  >
    <template v-slot:content>
      <v-form>
        <v-row>
          <v-col>
            <v-text-field
              label="Contact Person"
              v-model="customer.contactName"
            />
            <v-text-field label="Company Name" v-model="customer.companyName" />
            <v-text-field label="Mobile" v-model="customer.mobile" />
            <v-text-field label="Phone" v-model="customer.phone" />
            <v-text-field label="Email" v-model="customer.email" />
            <v-text-field label="Address" v-model="customer.address" />
          </v-col>
        </v-row>
      </v-form>
    </template>
  </simple-dialog>
</template>

<script setup>
import { ref, reactive, onMounted, inject } from "vue";
import { getAll, create, update, letter } from "@/services/customerService";

const loader = inject("loader");

const tableData = reactive({
  headers: [
    { title: "S. No", key: "SNo" },
    { title: "Name", key: "contactName" },
    { title: "Company", key: "companyName" },
    { title: "Mobile", key: "mobile" },
    { title: "Phone", key: "phone" },
    { title: "Email", key: "email" },
    { title: "Address", key: "address" },
    { title: "Actions", key: "Actions" },
  ],
  data: [],
  itemsPerPage: 15,
  totalItems: 0,
});

const customer = reactive({
  customerId: 0,
  contactName: "",
  companyName: "",
  phone: "",
  mobile: "",
  email: "",
  address: "",
});

const dlgCustomer = ref(null);

async function getData() {
  loader.show();

  let result = await getAll().finally(() => {
    loader.hide();
  });

  tableData.data = result;
}

function newCustomer() {
  customer.customerId = 0;
  customer.contactName = "";
  customer.companyName = "";
  customer.mobile = "";
  customer.address = "";
  customer.phone = "";
  customer.email = "";

  dlgCustomer.value.show();
}

function editData(item) {
  customer.customerId = item.customerId;
  customer.contactName = item.contactName;
  customer.companyName = item.companyName;
  customer.mobile = item.mobile;
  customer.address = item.address;
  customer.phone = item.phone;
  customer.email = item.email;

  dlgCustomer.value.show();
}

async function saveData() {
  loader.show();

  var result = null;

  if (customer.customerId == 0) {
    result = await create(customer).finally(() => {
      loader.hide();
    });
  } else {
    result = await update(customer.customerId, customer).finally(() => {
      loader.hide();
    });
  }

  console.log(result);
  await getData();
}

async function printForm(customer) {
  console.log(customer);

  loader.show();
  var form_data = await letter(customer.customerId).finally(() => {
    loader.hide();
  });

  console.log(form_data);

  var form_template = "";

  fetch("/bgt/reports/customer_form.html")
    .then((response) => response.text())
    .then((data) => {
      form_template = data;

      form_template = form_template.replaceAll("#FORMNO", `R\\${form_data.hijriYear}\\${customer.customerId}`);
      form_template = form_template.replaceAll("#COMPANYNAME", `${customer.companyName}`);
      form_template = form_template.replaceAll("#CONTACTPERSON", `${customer.contactName}`);
      form_template = form_template.replaceAll("#CONTACTNO", `${customer.mobile} - ${customer.phone}`);
      form_template = form_template.replaceAll("#ADDRESS", `${customer.address}`);
      form_template = form_template.replaceAll("#DEPOSIT", `PKR ${form_data.tenderAmount}/-`);

      var newWindow = window.open("about:blank", "_blank");
      newWindow.document.write(form_template);
      newWindow.document.close();
    });
}

onMounted(async () => {
  await getData();
});
</script>
