import { apiCall } from "@/setup/webApi";

export const getActive = () => {
  return new Promise((resolve, reject) => {
    apiCall("get", `SharafForm/active`)
      .then((response) => {
        resolve(response);
      })
      .catch((ex) => {
        reject(ex);
      });
  });
};

export const getById = (id) => {
  return new Promise((resolve, reject) => {
    apiCall("get", `SharafForm/${id}`)
      .then((response) => {
        resolve(response);
      })
      .catch((ex) => {
        reject(ex);
      });
  });
};

