export const addRegistration = (id, itsId, remarks) => {
  const formData = {
    memberItsId: itsId,
    remarks: remarks
  }

  return new Promise((resolve, reject) => {
    apiCall("post", `SharafForm/${id}`, formData)
      .then((response) => {
        resolve(response);
      })
      .catch((ex) => {
        reject(ex);
      });
  });
};

