<template>
    <v-container fluid>
        <v-btn @click="printReport" color="primary">Print</v-btn>
        <div v-html="report_template"></div>

    </v-container>
</template>

<script setup>
import { ref, defineProps, onMounted } from "vue";
import { getReportById } from "@/services/tenderService";
import moment from "moment";

const props = defineProps({
    tenderId: {
        type: String,
        required: true,
    },
    tender: {
        required: true,
    },
})

var report_template = ref("");
var report = "";

var reportData = [];

async function getReport() {
    reportData = await getReportById(props.tenderId);
}

async function getTemplate() {
    var temp = "";

    fetch('/bgt/reports/tender.html').then(response => response.text()).then(data => {
        temp = data;

        temp = temp.replace("#HIJRIYEAR", `${props.tender.hijriYear}H`);
        temp = temp.replace("#TENDERDATE", new moment(props.tender.tenderDate).format("D-MMM-yyyy"));

        var table_template = "";

        fetch('/bgt/reports/tender_item.html').then(response => response.text()).then(data => {

            reportData.forEach(rpt => {
                var temp_item = data;

                temp_item = temp_item.replace("#ITEMNAME", rpt.itemName);

                var items = "";

                rpt.items.forEach(it => {
                    items += `<tr><td>${it.customerName}</td><td>${it.price}</td></tr>`;
                });

                temp_item = temp_item.replace("#ITEMS", items);

                table_template += temp_item;
            });

            temp = temp.replace("#ITEMTABLES", table_template);
            report_template.value = temp;
            report = temp;
        });
    });
}

function printReport() {
    var newWindow = window.open("about:blank", "_blank");
    newWindow.document.write(report);
    newWindow.document.close();
    newWindow.print();
}

onMounted(async () => {
    await getReport();
    await getTemplate();

    console.log(props.tender);
});
</script>