<template>
  <v-container fluid>
    <v-card>
      <v-card-text class="d-flex">
        <v-spacer></v-spacer>
        <v-btn @click="newData">
          <v-icon icon="mdi-plus" />Add Expense Head</v-btn
        >
      </v-card-text>
    </v-card>
    <v-card class="mt-4">
      <template v-if="expenseHeads.length == 0">
        <h4 class="ma-5 text-center">No Items Available</h4>
      </template>
      <v-list v-else>
        <v-list-item
          v-for="item in expenseHeads"
          :key="item.expenseHeadId"
          :title="item.expenseHead"
        >
          <template v-slot:append>
            <v-btn icon="mdi-delete" variant="text" size="small" color="red" @click="deleteData(item)" />
          </template>
          <v-divider class="my-3" />
        </v-list-item>
      </v-list>
    </v-card>
  </v-container>

  <simple-dialog
    ref="dlgExpenseHead"
    title="New Expense Head"
    width="786"
    @ok="saveData"
  >
    <template v-slot:content>
      <v-form>
        <v-row>
          <v-col>
            <v-text-field
              label="Expense Head"
              v-model="expenseHead.expenseHead"
            />
          </v-col>
        </v-row>
      </v-form>
    </template>
  </simple-dialog>
</template>

<script setup>
import { ref, reactive, onMounted, inject } from "vue";
import {
  getExpenseHeads,
  newExpenseHead,
  editExpenseHead,
  deleteExpenseHead,
} from "@/services/tenderService";

const loader = inject("loader");

const expenseHeads = ref([]);
const expenseHead = reactive({
  expenseHeadId: 0,
  expenseHead: "",
});

const dlgExpenseHead = ref(null);

async function getData() {
  let result = await getExpenseHeads();
  expenseHeads.value = result;
}

async function saveData() {
  loader.show();

  var result = null;

  if (expenseHead.expenseHeadId == 0) {
    result = await newExpenseHead(expenseHead).finally(() => {
      loader.hide();
    });
  } else {
    result = await editExpenseHead(
      expenseHead.expenseHeadId,
      expenseHead
    ).finally(() => {
      loader.hide();
    });
  }

  console.log(result);
  await getData();
}

async function deleteData(expenseHead) {
  loader.show();

  var result = null;

  result = await deleteExpenseHead(expenseHead.expenseHeadId).finally(() => {
    loader.hide();
  });

  console.log(result);
  await getData();
}

function newData() {
  expenseHead.expenseHeadId = 0;
  expenseHead.expenseHead = "";

  dlgExpenseHead.value.show();
}

onMounted(async () => {
  await getData();
});
</script>
