<template>
  <v-container fluid>
    <v-card>
      <v-card-text>
        <h2 class="mb-4" v-if="'hijriYear' in tender">
          Tender {{ tender.hijriYear }}H
        </h2>

        <v-btn-toggle v-model="selectedIndex" color="primary" class="mb-5" density="compact" variant="elevated" elevation="2">
          <v-btn>Entry Form</v-btn>
          <v-btn>Report</v-btn>
        </v-btn-toggle>

        <entry-form v-if="selectedIndex == 0" :tender-id="tenderId" />
        <report-form v-if="selectedIndex == 1" :tender="tender" :tender-id="tenderId" />
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script setup>
import EntryForm from "./Components/EntryForm.vue";
import ReportForm from "./Components/ReportForm.vue";

import { ref, onMounted } from "vue";
import { getTenderById } from "@/services/tenderService";
import { useRoute } from "vue-router";

const route = useRoute();

const tenderId = route.params.id;

var tender = ref({});
var selectedIndex = ref(0);

async function loadTender() {
  let result = await getTenderById(tenderId);
  tender.value = result;
}

onMounted(async () => {
  await loadTender();
});
</script>
