import { apiCall } from "@/setup/webApi";

export const transfer_getAll = () => {
  return new Promise((resolve, reject) => {
    apiCall("get", `MemberRequest/transfer/all`)
      .then((response) => {
        resolve(response);
      })
      .catch((ex) => {
        reject(ex);
      });
  });
};

