import { apiCall } from "@/setup/webApi";

export const getAll = () => {
  return new Promise((resolve, reject) => {
    apiCall("get", "Tender/customer/all")
      .then((response) => {
        resolve(response);
      })
      .catch((ex) => {
        reject(ex);
      });
  });
};

export const create = (customer) => {
    return new Promise((resolve, reject) => {
      apiCall("post", "Tender/customer", customer)
        .then((response) => {
          resolve(response);
        })
        .catch((ex) => {
          reject(ex);
        });
    });
  };

export const update = (id, customer) => {
  return new Promise((resolve, reject) => {
    apiCall("patch", `Tender/customer/${id}`, customer)
      .then((response) => {
        resolve(response);
      })
      .catch((ex) => {
        reject(ex);
      });
  });
};

export const letter = (id) => {
  return new Promise((resolve, reject) => {
    apiCall("get", `Tender/customer/letter/${id}`)
      .then((response) => {
        resolve(response);
      })
      .catch((ex) => {
        reject(ex);
      });
  });
};
