import { apiCall } from "@/setup/webApi";

export const getAllTender = () => {
  return new Promise((resolve, reject) => {
    apiCall("get", "Tender/master/all")
      .then((response) => {
        resolve(response);
      })
      .catch((ex) => {
        reject(ex);
      });
  });
};

