<template>
  <template v-for="detail in tenderDetailData" :key="detail.customerId">
    <div class="detail">
      <h4 class="mb-4">({{ detail.customerId }}) {{ detail.companyName }} - {{ detail.customerName }}</h4>
      <v-form @submit.prevent="">
        <v-row>
          <template v-for="item in detail.items" :key="item.itemId">
            <v-col>
              <v-text-field
                :label="item.itemName"
                v-model="item.price"
                hide-details
              />
            </v-col>
          </template>
          <v-col md="2">
            <v-btn type="submit" @click="() => saveDetail(detail)">Save</v-btn>
          </v-col>
        </v-row>
      </v-form>
    </div>
  </template>
</template>

<script setup>
import { ref, onMounted, inject, defineProps } from "vue";
import {
  getTenderDetail,
  getAllItems as getItems,
  newTenderDetail,
} from "@/services/tenderService";
import { getAll as getCustomers } from "@/services/customerService";

const loader = inject("loader");
const snackbar = inject("snackBar");

const props = defineProps({
  tenderId: {
    type: String,
    required: true,
  },
});

const tenderDetail = ref([]);
const customers = ref([]);
const items = ref([]);

const tenderDetailData = ref([]);

async function loadDetail() {
  let result = await getTenderDetail(props.tenderId);

  tenderDetail.value = result;
}

async function loadCustomers() {
  let result = await getCustomers();

  customers.value = result;
}

async function loadItems() {
  let result = await getItems();

  items.value = result;
}

async function saveDetail(item) {
  loader.show();

  let result = await newTenderDetail(item)
    .then(() => snackbar.success("Prices have been saved successfully!"))
    .finally(() => {
      loader.hide();
    });

  console.log(result);
}

onMounted(async () => {
  loader.show();
  await loadCustomers();
  await loadItems();
  await loadDetail();
  loader.hide();

  customers.value.forEach((cust) => {
    var obj = {
      tenderId: props.tenderId,
      customerId: cust.customerId,
      customerName: cust.contactName,
      items: [],
    };

    items.value.forEach((item) => {
      var tender = tenderDetail.value.find(
        (x) =>
          x.tenderId == props.tenderId &&
          x.customerId == cust.customerId &&
          x.itemId == item.itemId
      );

      obj.items.push({
        tenderDetailId: tender?.tenderDetailId ?? 0,
        itemId: item.itemId,
        itemName: item.itemName,
        price: tender?.price ?? 0,
      });
    });

    tenderDetailData.value.push(obj);
  });
});
</script>

<style>
.detail {
  border-radius: 5px;
  border: 1px Solid #d6d6d6;
  padding: 10px;
  margin-bottom: 10px;
}
</style>
